.stars, .twinkling {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    display: block;
    overflow-y: scroll;
}

.stars {
    /* background: bg-color bg-image position/bg-size bg-repeat bg-origin bg-clip bg-attachment initial|inherit; */
    background: #000 url(stars.png) repeat top center;
    z-index: 0; 
}

.twinkling {
    background: transparent url(twinkling.png) repeat top right;
    z-index: 1;
    animation: move-twink 520s linear infinite;
}

@keyframes move-twink {
    from {background-position: 0 0;}
    to {background-position: -10000px 5000px;}
}

.Credentials {
    background-image: -webkit-linear-gradient(35deg, #02aab0, #00cdac 52%);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    display: inline-block;
    align-items: center;
    text-decoration: none;
    padding: 0 1rem;
    height: 100%;
    cursor: pointer;
    font-size: 4.39vh;
    font-weight: bold;
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    -webkit-animation-timing-function: ease-in-out;
    animation-timing-function: ease-in-out;
    animation-iteration-count: infinite;
    -webkit-animation-iteration-count: infinite;
}

.Credentials:hover {
    cursor: pointer;
    animation-name: bounceEffect;
    -webkit-animation-name: bounceEffect;
    -moz-animation-name: bounceEffect;
}

@keyframes bounceEffect {
    /*At what percentage of the animation duration will the task occur, 
    eg: when animation is 40% complete, letters bounce 18px upwards*/
    0%, 100%, 20%, 50%, 80% { 
		-webkit-transform: translateY(0);
		-ms-transform:     translateY(0);
		transform:         translateY(0)
	}
	40% {
		-webkit-transform: translateY(-18px);
		-ms-transform:     translateY(-18px);
		transform:         translateY(-18px)
	}
	60% {
		-webkit-transform: translateY(-7px);
		-ms-transform:     translateY(-7px);
		transform:         translateY(-7px)
	}
}

