.wavingHand {
    animation-name: handWave;
    -webkit-animation-name: handWave;
    animation-duration: 2.5s;
    -webkit-animation-duration: 2.5s;
    animation-iteration-count: infinite;
    -webkit-animation-iteration-count: infinite;
    /* transform-origin: x-axis | y-axis | (z-axis|initial|inherit);
    pivot around the bottom left of the palm */
    transform-origin: 70% 70%;
    /*with out putting the element in an inline- block the animation won't work*/
    display: inline-block;
}

@keyframes handWave {
    0%  { transform: rotate( 0.0deg) }
    10% { transform: rotate(14.2deg) }

    20% { transform: rotate(-8.5deg) }
    30% { transform: rotate(14.2deg) }
    40% { transform: rotate(-5.2deg) }
    50% { transform: rotate(10.0deg) }
    60% { transform: rotate(-5.2deg) }
    70% { transform: rotate( 0.0deg);}
    /* Now pause */
    /* No reset then start again */
    100% { transform: rotate(0.0deg) }
}

@-webkit-keyframes handWave {
    0%  { -webkit-transform: rotate( 0.0deg) }
    10% { -webkit-transform: rotate(14.2deg) }

    20% { -webkit-transform: rotate(-8.5deg) }
    30% { -webkit-transform: rotate(14.2deg) }
    40% { -webkit-transform: rotate(-5.2deg) }
    50% { -webkit-transform: rotate(10.0deg) }
    60% { -webkit-transform: rotate(-5.2deg) }
    70% { -webkit-transform: rotate( 0.0deg);}
    /* Now pause */
    /* No reset then start again */
    100% { -webkit-transform: rotate(0.0deg) }
}


