.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  /* reference to App-logo made below */
  .App-logo { 
    animation: App-logo-spin infinite 25s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
 
@keyframes App-logo-spin { /*Used in line 13*/
  from {
    transform: rotate(0deg);
  } 
  to {
    transform: rotate(360deg);
  }
}
